.App {
  text-align: center;
  background-color: white;
  color: #333;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  transition: background-color 0.3s, color 0.3s;
}

.App.dark-mode {
  background-color: black;
  color: #f0f0f0;
}

.app-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 20px;
  background-color: inherit;
}

.header-left {
  display: flex;
  align-items: center;
}

.app-title {
  font-size: clamp(1rem, 4vw, 2rem);
  font-weight: bold;
  margin: 0;
  margin-left: 10px;
  text-align: left;
}

.warning-symbol {
  font-size: clamp(1rem, 4vw, 1.5rem);
  color: #ffcc00;
  cursor: pointer;
  margin-left: 15px; /* Add some right margin to the warning symbol */

}



.btc-address {
  text-decoration: underline;
  text-decoration-thickness: 1px;
  text-underline-offset: 2px;
  color: #e4980adc;
  font-weight: bold;
  display: block;
  margin-top: 1em; /* Add spacing between label and address */
  font-family: monospace; /* Optional: makes it look more like code */
  line-height: 1; /* Adjusts line-height to reduce extra spacing */
  margin-bottom: 1em; /* Add spacing between label and address */

}


.btc-address-container {
  display: inline-flex;
  align-items: center;

}

ul li {
  margin-bottom: 0.5em; /* Adjust this value as needed */
}
.copy-button {
  margin-left: 0.9em; /* Space between address and button */
  line-height: 1; /* Ensures button height matches */
  padding: 0.2em 0.6em; /* Optional: adjust padding for balanced height */
  vertical-align: middle; /* Aligns with the text baseline */
}



.warning-symbol:hover {
  color: #a09e9e;
}


.theme-toggle {
  background: none;
  border: none;
  cursor: pointer;
  padding: 5px;
  color: #333;
  transition: color 0.3s;
  margin-left: 20px;
}


.dark-mode .theme-toggle {
  color: #f0f0f0;
}


/* New styles for the icon animation */
.theme-toggle svg {
  transition: transform 0.7s ease-in-out;
}

.theme-toggle:hover svg {
  transform: rotate(260deg);

}



.tabs {
  padding: 10px 0;
}

.tabs button {
  margin: 0 5px;
  padding: 5px 10px;
  font-size: clamp(0.8rem, 2vw, 1rem);
  cursor: pointer;
  background-color: #f0f0f0;
  color: #333;
  border: none;
  transition: background-color 0.3s, color 0.3s;
}

.dark-mode .tabs button {
  background-color: #333;
  color: #f0f0f0;
}

.dark-mode .tabs .active {
  background-color: #007bff;
  color: #f0f0f0;
}

.dark-mode button:hover {
  background-color: #b1b1b1;
  color: #f0f0f0;
}

button:hover {
  background-color: #b1b1b1;
  color: #f0f0f0;
}


.dark-mode button.theme-toggle:hover {
  background-color: unset; /* No background change on hover */
  color:white;
 
}


button.theme-toggle:hover {
  background-color: white; /* No background change on hover */
  color:black;
 
}

.tabs .active {
  background-color: #007bff;
  color: white;
}

.image-container {
  width: 100%;
  flex-grow: 1;
  position: relative;
  overflow: hidden;
}

.image-container img {
  position: absolute;
  top: 50%;
  left: 50%;
  width: auto;
  height: 100%;
  transform: translate(-50%, -50%);
  object-fit: contain;
  transition: opacity 0.2s ease-in-out;
}

.image-container img.hidden {
  opacity: 0;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: white;
  color: #333;
  padding: 20px;
  border-radius: 8px;
  max-width: 500px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  text-align: left;
  transition: background-color 0.3s, color 0.3s;
}

.dark-mode .modal-content {
  background-color: #333;
  color: #f0f0f0;
}

.modal-content h2 {
  color: #ffcc00;
  margin-bottom: 10px;
}

.modal-content p {
  font-size: 1rem;
  line-height: 1.6;
}

.close-button {
  background-color: #ffcc00;
  border: none;
  padding: 10px 20px;
  font-size: 1rem;
  color: white;
  cursor: pointer;
  margin-top: 10px;
  display: block;
}

.close-button:hover {
  background-color: #ff9900;
}



.refresh-button {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0.5rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease;
}


.dark-mode .refresh-button {
  color: white;
}

.refresh-button.refreshing svg {
  animation: spin 1s linear infinite;
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}

.loading-spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 1.2rem;
  color: #666;
}

.dark-mode .loading-spinner {
  color: #999;
}

.header-right {
  display: flex;
  gap: 1rem;
  align-items: center;
}


@media (max-width: 768px) {
  .image-container img {
    object-fit: contain;
    width: auto;
    height: auto;
    max-width: 100%;
    max-height: 100%;
  }
}


